.contact-form {
  width: 600px;
  flex-direction: column;
  margin: 0 auto;
  padding: 40px;
  display: flex;
  box-shadow: 0 10px 10px #ccc;
}

.contact-form-input {
  height: 40px;
  background-color: tomato;
  border: 1px solid #303030;
  margin-bottom: 20px;
  padding-left: 15px;
}

.contact-form-message {
  height: 120px;
  resize: none;
  border: 1px solid #303030;
  margin-bottom: 20px;
  padding: 15px;
}

.contact-form-submit {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #303030;
  border-radius: 4px;
  align-self: center;
  padding: 10px 20px;
  transition: transform .25s linear;
}

.contact-form-submit:hover, .contact-form-submit:focus {
  transform: scale(1.05);
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(93, 135, 129, .74);
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.close-btn {
  cursor: pointer;
  width: 25px;
  height: 25px;
  background-color: #7fffd4;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 40px;
  right: 40px;
}

